.header__nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    
  }
  
  
  
  .nav__content {
    width: 75%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-bottom: 1px solid #1f967b;
    border-top: 1px solid #1f967b;
  }
  
  .nav__list {
    display: -webkit-flex;
    display: flex;
    list-style: none;
  }
  
  .nav__list li {
    margin: 0 1.2rem;
  }
  
  .nav__link {
    text-decoration: none;
    color: #000;
    padding-bottom: 0.3rem;
    transition: all .4s;
  }
  
  
  
 
  
  
  
  
  
  
h1{
  font-size: 30px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 300;
  text-align: center;
  margin-bottom: 15px;
}
table{
  width:100%;
  table-layout: fixed;
}
.tbl-header{
  background-color: rgba(255,255,255,0.3);
 }
.tbl-content{
  height:300px;
  overflow-x:auto;
  margin-top: 0px;
  border: 1px solid rgba(255,255,255,0.3);
}
th{
  padding: 20px 15px;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
}
td{
  padding: 15px;
  text-align: left;
  vertical-align:middle;
  font-weight: 300;
  font-size: 12px;
  color: #fff;
  border-bottom: solid 1px rgba(255,255,255,0.1);
}


/* demo styles */
body{
  background: linear-gradient(to right, #25c481, #25b7c4);
  font-family: 'Roboto', sans-serif;
}
section{
  margin: 50px;
}


/* follow me template */
.made-with-love {
  margin-top: 40px;
  padding: 10px;
  clear: left;
  text-align: center;
  font-size: 10px;
  font-family: arial;
  color: #fff;
}
.made-with-love i {
  font-style: normal;
  color: #F50057;
  font-size: 24px;
  position: relative;
  top: 2px;
}
.made-with-love h2 {
  font-size: 25px;
}
.made-with-love span {
  font-size: 20px;
}
.made-with-love a:hover {
  text-decoration: underline;
}


/* for custom scrollbar for webkit browser*/

::-webkit-scrollbar {
    width: 6px;
} 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
} 
::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
}

.tr-hover:hover{
  background-color: rgba(255,255,255,0.3);
}

@media screen and (max-width:740px){
  .tbl-content{
      min-width: 150%;
      max-width: 50%;
  }
}
@media screen and (max-width:490px){
  .tbl-content{
      min-width: 200%;
      max-width: 50%;
  }
}
@media screen and (max-width:368px){
  .tbl-content{
      min-width: 250%;
      max-width: 50%;
  }
    
}
@media screen and (max-width:326px){
  .tbl-content{
      min-width: 300%;
      max-width: 50%;
  }

}
